.hero-section {
	height: 70vh;
	display: flex;
	align-items: center;
	justify-content: space-between;

	&__left {
		_padding: 3rem 0;
		width: 40%;
	}

	&__right {
		_padding: 3rem 0;
		width: 50%;
		text-align: left;
	}

	.hero {
		&__title {
			font-weight: 500;
			font-size: 6rem;
			padding: 2rem 1;
			color: inherit;
			&--sub {
				display: block;
				font-size: 2rem;
				text-transform: uppercase;
				letter-spacing: 2px;
				font-weight: 400;
				color: $color-secondary;
			}
		}

		&__text {
			font-size: 3rem;
			line-height: 1.2;
			margin: 0;
			margin-left: 3rem;
		}
	}
}

.section-about {
	.about {
		&__img {
			width: 100%;
			max-width: 100%;
			border-radius: 1.6rem;
			filter: grayscale(0) brightness(0.5) contrast(1.1);
		}
	}
}

.scroll {
	&-down {
		display: inline-block;
		text-align: center;
		&__text {
			font-size: 1.6rem;
		}
		&__icon {
			width: 24px;
			animation: bounce 1s 3s infinite;

			img {
				width: 100%;
			}
		}
	}

	&-top {
		display: none;
		width: 4rem;
		_height: 4rem;
		text-align: center;
		position: fixed;
		bottom: 2.5rem;
		right: 2.5rem;
		border: none;
		padding: 0.8rem;
		border-radius: 3px;
		transform: rotate(180deg);
		z-index: 1000;
		transition: all 0.2s ease;
		img {
			width: 100%;
		}

		&:hover {
			transform: rotate(180deg) scale(1.1);
		}
	}
}

.work {
	&__list {
		list-style: none;
		padding: 0;
		display: flex;
		flex-direction: column;
	}

	&__item {
		font-size: 1.6rem;

		&:not(:last-child) {
			margin-bottom: 0.6rem;
		}
	}

	&__link {
		&:link,
		&:visited {
			font-size: 1.6rem;
			font-weight: bold;
		}
	}
}

.show {
	display: block;
}
