.navigation {
	_width: 80vw;
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 10rem;

	&__brand {
		img {
			width: 100%;
		}
	}

	&__nav {
		_width: 50vw;
	}

	&__list {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
	}

	&__item {
		list-style: none;

		&:not(:last-child) {
			margin-right: 1.2rem;
		}
	}
}
