.footer {
	&__nav {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__list {
		display: flex;
		align-items: center;
		justify-content: center;
		list-style: none;
	}

	&__item {
		&:not(:last-child) {
			margin-right: 6rem;
		}
	}

	&__link {
		font-weight: 700;
	}
}
